// Get offers block

%get-offers-item{
  display: inline-block;
  vertical-align: top;
  height: 49px;
  color: #fff;
  font-weight: 700;
}

.get-offers{
  font-size: 0;
  &__button{
    @extend %get-offers-item;
    width: 58%;
    background-color: #0081CC;
    text-transform: uppercase;
    font-size: 19px;
    line-height: 49px;
    text-align: center;
    margin-right: 6px;
    &:before{
      content: '';
      display: inline-block;
      width: 23px;
      height: 23px;
      background-image: url(../images/sale-icon.png);
      vertical-align: middle;
      margin-right: 7px;
    }
    &:hover,
    &:focus{
      color: #fff;
      background-color: #2B9ECC;
    }
  }
  &__date-persons{
    @extend %get-offers-item;
    font-size: 14px;
    width: calc( 42% - 6px );
    background-color: #AD3A4B;
    padding:{
      top: 11px;
      bottom: 11px;
      right: 17px;
      left: 30px;
    }
    @media screen and (max-width: 1149px) and (min-width: 992px){
      padding-left: 15px;
    }
    &:after{
      content: '';
      display: table;
      clear: both;
    }
  }
  &__datepicker{
    width: 116px;
    border: none;
    font-size: 17px;
    outline: none;
    background: transparent url('../images/calendar-white.svg') no-repeat;
    background-position: 0px 5px;
    background-size: 15px;
    text-align: right;
  }
}

.jcf-select-get-offers__persons-select{
  width: auto;
  min-width: 90px;
  font-weight: 400;
  border-color: #8C1A2C;
  height: auto;
  float: right;
  .jcf-select-text{
    color: #AD3A4B;
    margin:{
      top: 4px;
      bottom: 4px;
    }
  }
  .jcf-select-opener {
    width: 22px;
    &:after {
      border-top-color: #AD3A4B;
      border-width: 6px 5px 0;
      top: 10px;
      right: 6px;
    }
  }
}

@media screen and (max-width: 700px){
  %get-offers-item{
    display: block;
    width: 100%;
    margin-bottom: 5px;
  }
}

//Offers navigation

.offers-navigation{
  background-color: #EEEEEE;
  padding: 9px 0;
  &__list{
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: center;
  }
  &__item{
    display: inline-block;
    vertical-align: top;
    margin-right: 29px;
    font-size: 12px;
    &:last-child{
      margin-right: 0;
    }
    @media screen and (max-width: 450px){
      display: block;
      margin: 0 0 10px;
      font-size: 14px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .active>a{
    font-weight: 700;
  }
  &__link{
    color: #000;
    &:hover,
    &:visited,
    &:focus{
      color: #000;
    }
  }
}



